.App {
  display: flex;
  justify-content: center;
}

.calculator {
  margin-top: 30%;
  display: grid;
  grid-template-columns:  1fr 1fr 1fr 1fr;
}

.calculator > * {
  border: 1px solid purple;
  padding: 10px;
}

.row {
  grid-column: 1/-1;
}

#display {
  text-align: right;
}

#clear {
  text-align: center;
}